<script>
  import app from "../stores/app";
  import theme from "../stores/theme";

  $: bg = $theme.mode == "dark" ? "bg-brand-darker-black" : "bg-gray-200";
  $: roleText =
    $theme.mode == "dark" ? "text-brand-yellow" : "text-brand-darker-black";
  $: roleBorder =
    $theme.mode == "dark" ? "border-brand-yellow" : "border-brand-darker-black";

  export let id;
  export let title = "Project Title";
  export let descEN =
    "Lorem ipsum dolor sit amet consectetur, adipisicing elit. Culpa architecto illum perferendis sapiente laboriosam sunt unde tempore, repellendus reiciendis fugiat voluptates quas aspernatur voluptatem dolore quidem molestias pariatur blanditiis quisquam. Commodi!";
  export let role = "Front-End Developer";
  export let tech = "Html, Css, Javascript, Sass, React, Gatsby";
  export let year = "2020";
  export let url = "#";
  export let descFR;
</script>

<style>
  .work-card {
    border-top-right-radius: 50px;
    border-bottom-left-radius: 50px;
  }
  .work-card:hover {
    transform: scale(1.03);
  }
</style>

<div
  data-aos="fade-up"
  data-aos-offset="0"
  data-aos-delay={id * 50}
  data-aos-duration="500"
  class={`aos-item work-card ${bg} relative z-50 shadow-lg mb-16 flex flex-col justify-between transition-transform duration-500`}>
  <div>
    <img
      src="./images/card-tablets.svg"
      class="absolute left-0 opacity-50"
      style="top:50%; transform: translate(5px, -50%);"
      alt={`Card Tablet #${id}`} />
    <h1
      class="border-brand-red border-t-4 pt-4 pl-12 text-brand-red text-xl mb-6
      w-48 whitespace-no-wrap">
      {title}
    </h1>
    <div class="pl-12 pr-4">
      {#if $app.lang === 'en'}
        <p class="ml-2 mb-6" style="min-height: 150px;">{descEN}</p>
      {:else}
        <p class="ml-2 mb-6" style="min-height: 150px;">{descFR}</p>
      {/if}
    </div>
  </div>

  <div>
    <div class="pl-12 pr-4">
      <p class={`ml-2 pb-2 ${roleText}`}>Role: {role}</p>
      <div class={`ml-2 border-b ${roleBorder} mb-6 opacity-50`} />
      <p class="ml-2">Technologies: {tech}</p>
    </div>
    <div class="pl-12 ml-2 mt-10 flex justify-between">
      <p>{year}</p>
      <a
        href={url}
        target="_blank"
        class="flex text-brand-green items-center border-brand-green border-b-4
        pb-4 pr-4 w-48 justify-end">
        {#if $app.lang === 'en'}
          <span> SEE PROJECT </span>
        {:else}<span> VOIR LE PROJET </span>{/if}
        <i class="ml-3 fas fa-arrow-right" />
      </a>
    </div>
  </div>
</div>
