<script>
  import SectionHeading from "../shared/SectionHeading.svelte";
  import axios from "axios";
  import theme from "../stores/theme";
  import app from "../stores/app";
  $: bg = $theme.mode == "dark" ? "bg-brand-darker-black" : "bg-gray-200";

  let sending = false;

  let message = {
    name: "",
    email: "",
    tel: "",
    object: "",
    content: "",
  };

  let handleSubmit = () => {
    sending = true;

    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${process.env.SEND_GRID_KEY}`,
    };

    var data = {
      personalizations: [
        {
          to: [
            {
              email: "katendeglory@gmail.com",
            },
          ],
        },
      ],
      from: {
        email: "glorykat777@gmail.com",
      },
      subject: `🔔 PORTFOLIO ALERT 🔔 From ${message.name} on The Subject ${message.object}`,
      content: [
        {
          type: "text/plain",
          value: `
Name : ${message.name}\n
Telephone : ${message.tel}\n
Email : ${message.email}\n
Subject : ${message.object}\n
------------------\n\n\n
${message.content}
          `,
        },
      ],
    };

    axios
      .post(
        "https://cors-anywhere.herokuapp.com/https://api.sendgrid.com/v3/mail/send",
        data,
        { headers }
      )
      .then((response) => {
        sending = false;
        alert(
          `Thank You For the Message ${message.name}, I'll get back to you`
        );
        message.content = "";
        message.email = "";
        message.name = "";
        message.object = "";
        message.tel = "";
      })
      .catch((error) => {
        sending = false;
        alert(
          `Sorry, There was an unexpected Error 😞, In a sharing mood? Please report it to katendeglory@gmail.com, I'll fix it so others won't have the same issue`
        );
        console.log(error.message);
      });
  };
</script>

<style>
  .contact-card {
    /* border-top-right-radius: 50px; */
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 0px;
  }

  @media (min-width: 768px) {
    .contact-card {
      border-radius: 0;
      border-top-right-radius: 25px;
      border-bottom-left-radius: 25px;
    }
  }
</style>

<div class="relative" id="contacts">
  <div class="max-w-7xl mx-auto px-4 py-6 overflow-x-hidden" id="my-work">
    {#if $app.lang === 'en'}
      <SectionHeading text1="REACH" text2="ME" />
    {:else}
      <SectionHeading text1="ME" text2="CONTACTER" />
    {/if}

    <h1
      class={`aos-item text-base mt-16 mb-24 md:max-w-4xl flex flex-col mx-auto
      shadow-sm py-4 px-4 rounded-md ${bg} border-t border-brand-green`}
      data-aos="fade-up"
      data-aos-duration="600">
      <div class="flex items-center mb-4 text-brand-green">
        <i class="fas fa-info-circle text-2xl text-brand-green block mr-3" />
        {#if $app.lang === 'en'}
          <span> Thanks for visiting my website! </span>
        {:else}<span> Merci de visiter mon petit coin du web! </span>{/if}
      </div>

      {#if $app.lang === 'en'}
        <div>
          Hey there! Would you like to chat? Me too, Just fill the form below or
          give me a call
          <i class="fas fa-phone" />
          using the information provided here
          <i class="fas fa-hand-point-down px-1" />
          .
          <div class="lg:mb-1 mb-3" />
          And I promise I'll get back to you as soon as I can, and hear all
          about your amazing ideas
          <i class="fas fa-smile-wink" />
          !
        </div>
      {:else}
        <div>
          Hey! voulez discuter avec moi? Moi aussi, Remplissez le petit
          formulaire en bas ou appelez moi
          <i class="fas fa-phone" />
          utilisant les informations en bas
          <i class="fas fa-hand-point-down px-1" />
          .
          <div class="lg:mb-1 mb-3" />
          Et je promet de vous repondre aussi vite que possible et prendre le
          temps d'ecouter toutes vos merveilluses idées sur lesquelles nous
          travaillerons ensemble!
          <i class="fas fa-smile-wink" />
          !
        </div>
      {/if}
    </h1>

    <div class="flex flex-col md:flex-row">
      <div
        data-aos="fade-right"
        class={`aos-item contact-card overflow-hidden ${bg} flex flex-col shadow-sm relative w-full max-w-sm sm:max-w-none sm:w-112 md:w-160 mx-auto`}>
        <!-- Top -->
        <div class="w-full h-56 overflow-hidden relative">
          <img
            src="./images/profile.jpg"
            class="w-full h-full object-cover object-top shadow-lg"
            alt="Glory Katende" />
          <div
            class="w-full h-full absolute bg-brand-green top-0 left-0"
            style="opacity:.14;" />
        </div>
        <!-- Bottom -->
        <div
          class="w-full pl-10 pt-6 pb-6 border-t-4 border-brand-red flex
          flex-col justify-center">
          <div class="flex items-center mb-4" style="opacity:0.9;">
            <i class="fas fa-phone-square text-brand-green text-4xl mr-3" />
            <a href="tel:+26878635116">268 78635116</a>
          </div>
          <div class="flex items-center mb-4">
            <i class="fab fa-whatsapp text-brand-green text-4xl mr-3" />
            <a href="https://wa.me/26878635116" target="_blank">268 78635116</a>
          </div>
          <div class="flex items-center">
            <i class="fas fa-envelope-square text-brand-green text-4xl mr-3" />
            <a href="mailto:katendeglory@gmail.com" target="_blank">
              katendeglory@gmail.com
            </a>
          </div>
        </div>

        <img
          src="./images/card-tablets-yellow.svg"
          class="absolute left-0 opacity-50"
          style="top:50%; transform: translate(-10px, -15%);"
          alt="tablets" />
      </div>

      <form
        data-aos="fade-left"
        data-aos-delay="150"
        on:submit|preventDefault={handleSubmit}
        class="aos-item w-full mt-8 md:mt-0 md:ml-6">
        <h1 class="my-5 opacity-75 hidden md:flex items-center">
          <i class="fas fa-info-circle mr-3 text-3xl text-brand-green" />
          {#if $app.lang === 'en'}
            <span> I hereby solemnly declare to never spam you! :) </span>
          {:else}
            <span>
              Je déclare solennellement de ne jamais vous envoyer de spam! :)
            </span>
          {/if}
        </h1>

        <div class="lg:flex">
          <input
            bind:value={message.name}
            type="text"
            class={`text-field w-full lg:w-1/2 lg:mr-4 mb-4 ${bg}`}
            required
            placeholder="Your Name (Votre Nom)" />
          <input
            bind:value={message.tel}
            type="tel"
            class={`text-field w-full lg:w-1/2 mb-4 ${bg}`}
            required
            placeholder="Telephone" />
        </div>

        <div class="lg:flex">
          <input
            bind:value={message.email}
            type="email"
            class={`text-field w-full lg:w-1/2 lg:mr-4 mb-4 ${bg}`}
            required
            placeholder="Email" />
          <input
            bind:value={message.object}
            type="text"
            class={`text-field w-full lg:w-1/2 mb-4 ${bg}`}
            required
            placeholder="Object" />
        </div>

        <div class="">
          <textarea
            bind:value={message.content}
            class={`text-field w-full mb-4 ${bg}`}
            required
            rows="6"
            placeholder="Message..." />
        </div>

        <div class="w-full flex justify-end">
          <button
            disabled={sending}
            class={`text-field text-brand-green flex items-center text-lg ${bg} w-full`}>
            {#if sending}
              Sending...
              <i class="ml-3 fas fa-circle-notch animate-spin fa-lg" />
            {:else}
              {#if $app.lang === 'en'}
                <span> Send Your Message </span>
              {:else}<span> Envoyer votre message </span>{/if}
              <i class="ml-3 fas fa-envelope fa-lg" />
            {/if}
          </button>
        </div>

        <h1 class="mt-8 flex items-center md:hidden opacity-50">
          <i class="fas fa-info-circle mr-3 text-3xl text-brand-green" />
          {#if $app.lang === 'en'}
            <span> I hereby solemnly declare to never spam you! :) </span>
          {:else}
            <span>
              Je déclare solennellement de ne jamais vous envoyer de spam! :)
            </span>
          {/if}
        </h1>
      </form>
    </div>
  </div>
  <!-- Absolute elements -->
</div>
