<script>
  import theme from "../stores/theme";
  $: bg = $theme.mode == "dark" ? "bg-brand-darker-black" : "bg-gray-200";

  export let icon = "react";
  export let toolName = "React JS";
  export let id;
</script>

<style>
  .skill-card {
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
  }
</style>

<div
  data-aos="zoom-in-up"
  data-aos-offset="0"
  data-aos-delay={id * 50}
  class="aos-item flex flex-col items-center sm:flex-row sm:items-center mb-10">
  <div
    class={`skill-card ${bg} h-20 w-20 sm:h-24 sm:w-24 flex items-center justify-center shadow-md`}>
    <img
      class=" h-16 w-16 sm:h-20 sm:w-20"
      src={`./icons/${icon}.svg`}
      alt={icon} />
  </div>

  <h1 class="mt-2 sm:mt-0 sm:ml-3 text-lg sm:text-xl">{toolName}</h1>
</div>
