<script>
  import theme from "../stores/theme";
  import { fly, slide, fade } from "svelte/transition";
  import app from "../stores/app";

  $: bg = $theme.mode == "dark" ? "bg-gray-800" : "bg-gray-200";
  let menuVisible = false;
</script>

<style>
  .abc {
    fill: none;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 3px;
  }
</style>

<header
  transition:fade={{ duration: 1000, delay: 6000 }}
  class="fixed left-0 top-0 w-full"
  style="z-index:999;">
  <div class="max-w-7xl mx-auto h-16 px-4 flex justify-between items-center">
    <a href="#home">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-6 h-6 fill-current"
        viewBox="0 0 40.503 31.496">
        <path
          d="M19.714,10.425,6.75,21.1V32.625A1.125,1.125,0,0,0,7.875,33.75l7.879-.02A1.125,1.125,0,0,0,16.874,32.6v-6.73A1.125,1.125,0,0,1,18,24.75h4.5a1.125,1.125,0,0,1,1.125,1.125V32.6a1.125,1.125,0,0,0,1.125,1.129l7.876.022a1.125,1.125,0,0,0,1.125-1.125V21.094L20.789,10.425A.857.857,0,0,0,19.714,10.425Zm20.477,7.257-5.878-4.845V3.1a.844.844,0,0,0-.844-.844H29.531a.844.844,0,0,0-.844.844V8.2l-6.3-5.179a3.375,3.375,0,0,0-4.289,0L.305,17.681A.844.844,0,0,0,.193,18.87l1.793,2.18a.844.844,0,0,0,1.189.115L19.714,7.542a.857.857,0,0,1,1.076,0l16.54,13.622a.844.844,0,0,0,1.188-.112l1.793-2.18a.844.844,0,0,0-.12-1.19Z"
          transform="translate(0.001 -2.254)" />
      </svg>
    </a>

    <!-- Desktop Navigation -->
    <div
      class={`flex items-center ${bg} px-3 h-12 md:px-10 shadow-sm rounded-full`}>
      <a
        class="mr-6 hidden md:inline-block transition-colors duration-500
        hover:text-brand-red"
        href="#my-work">
        {#if $app.lang === 'en'}<span>My Work</span>
        {:else}<span>Mes Projects</span>{/if}
      </a>
      <a
        class="mr-6 hidden md:inline-block transition-colors duration-500
        hover:text-brand-red"
        href="#skills">
        {#if $app.lang === 'en'}<span>Skills</span>
        {:else}<span>Expertise</span>{/if}
      </a>
      <a
        class="mr-6 hidden md:inline-block transition-colors duration-500
        hover:text-brand-red"
        href="#contacts">
        Contact
      </a>

      {#if $theme.mode == 'dark'}
        <button
          class="focus:outline-none"
          in:fade={{ duration: 1000 }}
          on:click={() => {
            theme.set({ mode: 'light' });
            const root = document.documentElement;
            root.style.setProperty('--thumbScroll', '#353d4a');
            document.body.style.backgroundColor = '#edf2f7';
            window.scrollBy({ top: 1, behavior: 'smooth' });
          }}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-6 h-6 fill-current"
            viewBox="0 0 35.998 36">
            <path
              d="M18,11.25A6.75,6.75,0,1,0,24.75,18,6.761,6.761,0,0,0,18,11.25Zm17.325,5.66-6.659-3.326,2.355-7.059a1.216,1.216,0,0,0-1.54-1.54L22.423,7.341,19.09.675a1.217,1.217,0,0,0-2.18,0L13.584,7.334,6.518,4.978a1.216,1.216,0,0,0-1.54,1.54l2.355,7.059L.675,16.91a1.217,1.217,0,0,0,0,2.18l6.659,3.326L4.978,29.482a1.216,1.216,0,0,0,1.54,1.54l7.059-2.355L16.9,35.325a1.217,1.217,0,0,0,2.18,0l3.326-6.659,7.059,2.355a1.216,1.216,0,0,0,1.54-1.54l-2.355-7.059L35.311,19.1a1.219,1.219,0,0,0,.014-2.187ZM24.363,24.363a9,9,0,1,1,0-12.727A9.009,9.009,0,0,1,24.363,24.363Z" />
          </svg>
        </button>
      {:else}
        <button
          class="focus:outline-none"
          in:fade={{ duration: 1000 }}
          on:click={() => {
            theme.set({ mode: 'dark' });
            const root = document.documentElement;
            root.style.setProperty('--thumbScroll', '#cbd5e0');
            document.body.style.backgroundColor = '#2d3748';
            window.scrollBy({ top: 1, behavior: 'smooth' });
          }}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-6 h-6 fill-current"
            viewBox="0 0 30 30">
            <path
              d="M18.7,30a17.576,17.576,0,0,0,13.038-5.554.715.715,0,0,0-.758-1.134c-8.143,1.386-15.621-4.194-15.621-11.54A11.592,11.592,0,0,1,22.01,1.553a.684.684,0,0,0-.246-1.3A18.883,18.883,0,0,0,18.7,0C9.433,0,1.913,6.71,1.913,15S9.421,30,18.7,30Z"
              transform="translate(-1.913)" />
          </svg>
        </button>
      {/if}
    </div>
  </div>
</header>

<!-- Mobile Naigation -->
<div
  transition:fly={{ x: 100, duration: 500, delay: 6000 }}
  class={`block md:hidden fixed right-0 bottom-0 px-4 pt-3 pb-2 mx-2 my-4 rounded-md shadow-lg ${bg}`}
  style="z-index:999;">

  <button
    class="focus:outline-none"
    on:click={() => {
      menuVisible = !menuVisible;
    }}>
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="w-6 h-6 stroke-current"
      viewBox="0 0 30 21">
      <g transform="translate(-3 -7.5)">
        <path class="abc" d="M4.5,18h27" />
        <path class="abc" d="M4.5,9h27" />
        <path class="abc" d="M4.5,27h27" />
      </g>
    </svg>
  </button>

  {#if menuVisible}
    <div
      transition:slide={{ duration: 100 }}
      class={`absolute mb-16 bottom-0 right-0 w-48 flex flex-col px-3 pt-4 pb-2 rounded-md shadow-lg ${bg} border-t-4 border-brand-green`}>
      <a
        class="mb-5"
        on:click={() => {
          menuVisible = false;
        }}
        href="#home">
        {#if $app.lang === 'en'}<span>Home</span>
        {:else}<span>Acceuil</span>{/if}
      </a>
      <a
        class="mb-5"
        on:click={() => {
          menuVisible = false;
        }}
        href="#my-work">
        {#if $app.lang === 'en'}<span>My Work</span>
        {:else}<span>Mes Projects</span>{/if}
      </a>
      <a
        class="mb-5"
        on:click={() => {
          menuVisible = false;
        }}
        href="#skills">
        {#if $app.lang === 'en'}<span>Skills</span>
        {:else}<span>Expertise</span>{/if}
      </a>
      <a
        class="mb-5"
        on:click={() => {
          menuVisible = false;
        }}
        href="#contacts">
        Contact
      </a>
    </div>
  {/if}
</div>

<!-- Language -->

<div class="fixed left-0 bottom-0 w-full" style="z-index:999">
  <div class="max-w-7xl mx-auto flex justify-start">
    <div
      transition:fly={{ x: -100, duration: 1000, delay: 1500 }}
      class={`px-3 pt-2 pb-1 mx-2 my-4 rounded-md shadow-lg ${bg}`}
      style="z-index:999;">
      {#if $app.lang === 'en'}
        <button class="focus:outline-none" on:click={() => app.setLang('fr')}>
          <svg class="w-6 h-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 3 2"><path fill="#ED2939" d="M0 0h3v2H0z"/><path fill="#fff" d="M0 0h2v2H0z"/><path fill="#002395" d="M0 0h1v2H0z"/></svg>
        </button>
      {:else}
      <button class="focus:outline-none" on:click={() => app.setLang('en')}>
        <svg class="w-6 h-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 30"><clipPath id="a"><path d="M0 0v30h60V0z"/></clipPath><clipPath id="b"><path d="M30 15h30v15zv15H0zH0V0zV0h30z"/></clipPath><g clip-path="url(#a)"><path d="M0 0v30h60V0z" fill="#012169"/><path d="M0 0l60 30m0-30L0 30" stroke="#fff" stroke-width="6"/><path d="M0 0l60 30m0-30L0 30" clip-path="url(#b)" stroke="#C8102E" stroke-width="4"/><path d="M30 0v30M0 15h60" stroke="#fff" stroke-width="10"/><path d="M30 0v30M0 15h60" stroke="#C8102E" stroke-width="6"/></g></svg>
      </button>
      {/if}
    </div>
  </div>
</div>
