<script>
  import theme from "./../stores/theme";
  import { onMount } from "svelte";
  $: bg = $theme.mode == "dark" ? "bg-gray-800" : "bg-gray-200";
  $: text = $theme.mode == "dark" ? "text-gray-400" : "text-gray-700";
  $: bgImage = $theme.mode == "dark" ? "light.svg" : "dark.svg";

  onMount(() => {
    if ($theme.mode == "dark") {
      document.body.style.backgroundColor = "#2d3748";
    } else {
      document.body.style.backgroundColor = "#edf2f7";
    }
  });
</script>

<div
  class={`${bg} ${text} tracking-wide min-h-screen`}
  id="home"
  style={`background-image: url(./images/circuit-${bgImage});`}>
  <slot />
</div>
