import { writable } from 'svelte/store';
import * as ls from 'local-storage';

const createStore = () => {
  const { set, subscribe, update } = writable({
    lang: ls.get('lang') || 'en'
  });

  return {
    subscribe,
    setLang: lang => update(prev => {
      ls.set('lang', lang);
      return ({ ...prev, lang });
    }),
  }
}

const app = createStore();

export default app;